.features h2 {
  width: 362px;
  .mg-auto();
  padding-bottom: 50px;
  .font-base(@fontName;normal;600;30px;30px);
  letter-spacing: -0.005em;
  color: #1f1f1f;
}

.features {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  padding-top: 150px;
  padding-bottom: 130px;
  background: #ffffff;
}

.features__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  .reset();
  margin: 0 auto;
}

.features__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 263px;
  max-width: 263px;
  margin: 0 19px;
}

.features__item h3 {
  .reset();
  .font-base(@fontNameBold;normal;bold;28px;38px);
  text-align: center;
  letter-spacing: -1.27273px;
  color: #0ad9c6;
}

.features__item p {
  .reset();
  .font-base(@fontName;normal;normal;22px;34px);
  text-align: center;
  letter-spacing: -0.5px;
  color: #000000;
}

@media (max-width: 1023px) {
  .features h2 {
    width: 290px;
    font-size: 24px;
    line-height: 41px;
  }

  .features__item {
    padding-bottom: 40px;
  }
}

@media (max-width: 767px) {
  .features__item p {
    font-size: 18px;
  }

  .features__item {
    padding-bottom: 30px;
  }
}
